.button {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  border: 1px solid black;
}

.button::before,
.button::after {
  position: absolute;
  content: "";
  display: block;
  width: 30px;
  height: 3px;
  background: black;
  top: 18.2px;
  left: 3.9px;
  transition: 0.5s all;
}
.button::before {
  transform: rotate(90deg);
}
.hover.button::before {
  transform: rotate(450deg);
}

.hover.button::after {
  transform: rotate(360deg);
}

.leave.button::before {
  transform: rotate(-450deg);
}

.leave.button::after {
  transform: rotate(-360deg);
}

.button:active {
  transform: scale(0.85);
  box-shadow: 0 0 3px black;
}
