.currentMonth {
  background: #e0e0e0;
}

.today {
  background: #767676;
  color: aliceblue;
}

.td {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border: 2px solid transparent;
}
.td:hover {
  border: 2px solid #767676;
}

.activeDay {
  border: 2px dotted black;
  font-weight: 900;
}
