.container {
  width: 500px;
  height: 300px;
  position: absolute;
  left: calc(50% - (90px));
  top: 100px;
  background: whitesmoke;
  box-shadow: 0 0 50px black;
  padding: 30px;
  border-radius: 10px;
}

.background {
  background: #000000b6;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.btnClose {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 5px;
  right: 5px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: none;
}

.btnClose::before,
.btnClose::after {
  content: "";
  display: block;
  width: 20px;
  height: 2px;
  position: absolute;
  background: #7a7a7a;
}

.btnClose::before {
  transform: rotate(45deg);
}

.btnClose::after {
  transform: rotate(-45deg);
}

.btnClose:hover.btnClose::before,
.btnClose:hover.btnClose::after {
  background: black;
}

.btnClose:active.btnClose::before {
  transform: rotate(45deg) scale(0.7);
  box-shadow: 0 0 3px black;
}

.btnClose:active.btnClose::after {
  transform: rotate(-45deg) scale(0.7);
  box-shadow: 0 0 3px black;
}

.confirmBtn {
  position: absolute;
  top: 315px;
  left: 145.5px;
  padding: 5px 46px;
  background: #2f4ef6;
  cursor: pointer;
}

@media (max-width: 991px) {
  .container {
    width: 400px;
    left: calc(50% - (37px));
  }
}

@media (max-width: 767px) {
  .container {
    top: 400px;
    left: calc(50% - (200px));
  }
}

@media (max-width: 480px) {
  .container {
    width: 300px;
    left: calc(50% - (150px));
  }
}

@media (max-width: 380px) {
  .container {
    width: 270px;
    left: calc(50% - (135px));
  }
}
