.todayButtonContainer {
  position: absolute;
  top: -30px;
  left: 0;
  width: 100%;
}

.todayButton {
  background: #e0e0e0;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  border: 2px solid #e0e0e0;
  font-weight: bold;
}

.todayButton:hover {
  cursor: pointer;
  border-color: #484848;
}
