.organizerContainer {
  /*width: 1200px;
  margin: 0 auto;
  display: flex;
  padding: 20px;*/
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  padding: 20px;
  grid-template-columns: 324px 1fr;
  height: 100%;
}

@media (max-width: 767px) {
  .organizerContainer {
    grid-template-columns: 1fr;
    grid-template-rows: 300px 1fr;
  }
}
