.tableHeaderDate {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 20px;
  cursor: pointer;
}

.tableHeaderDate span:first-child {
  margin-right: 5px;
}
.tableHeaderDate span:hover {
  text-decoration: underline;
}

.tableHeaderDate span:active {
  color: #787878;
}
