.buttonContainer {
  position: relative;
}

.buttonContainer:hover .calendarButton {
  border-left-color: black;
  border-bottom-color: black;
}

.buttonContainer:active .calendarButton {
  border-left-color: #ccc;
  border-bottom-color: #ccc;
}

.arrow {
  font-size: 30px;
  line-height: 19px;
  top: 0px;
  left: 0px;
  cursor: pointer;
  color: #a2a2a2;
  align-self: center;
  text-align: center;
}
.arrow:hover {
  color: #707070;
}

.arrow:active {
  color: black;
  text-shadow: none;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}
